import 'lib-flexible'
import FastClick from 'fastclick'
window.FastClick = FastClick
import Vue from 'vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'
Vue.use(VueResource)
Vue.use(VueRouter)

import list from './view/list.vue'
import comments from './view/comments.vue'
import detail from './view/detail.vue'
import recommenders from './view/recommender.vue'
import section from './view/section.vue'
import editors from './view/editor.vue'
import author from './view/author.vue'
import listDefault from './components/list-default.vue'
import listTheme from './components/list-theme.vue'
import mui from './mui.js'
window.mui = mui



import Swiper from 'swiper'
window.Swiper = Swiper

import filters from './filters'
Object.keys(filters).forEach((k) => Vue.filter(k, filters[k]))

/* eslint-disable no-new */
let App = Vue.extend({})
// 
//  HTML 
// var Apptest = Vue.extend({})
// 
// 
var router = new VueRouter()
// 
//  Vue.extend
// 
// 
router.map({
  '/': {
    component: list,
    subRoutes: {
      '/': {
        component: listDefault
      },
      '/theme/:id': {
        name: 'theme',
        component: listTheme
      },
      '/author/:id': {
        name: 'author',
        component: author
      }
    }
  },
  '/detail/:id': {
    name: 'detail',
    component: detail
  },
  '/comments': {
    component: comments
  },
  '/recommenders': {
    component: recommenders
  },
  '/section': {
    component: section
  },
  '/editors': {
    component: editors
  }
})
// 
//  App  #app 
router.start(App, '#app')
mui.init();


